import React from "react";
import { useSelector } from "react-redux";

export default function Plans(props) {
  const restaurant = useSelector((state) => state.restaurant);
  const { plan } = restaurant;
  const { twoPlan, fifteenPlan, thirtyPlan } = plan || {};
  return (
    <div className="ibox">
      <div className="ibox-content">
        {plan && (
          <table className="table table-bordered">
            <thead>
              <tr>
              <th scope="col">Plan</th>
              <th scope="col">Base Price</th>
              <th scope="col">Customer Price</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>2 Days</td>
                <td>
                  {"$"}
                  {twoPlan.base_2price}
                </td>
                <td>
                  {"$"}
                  {twoPlan.customer_2price}
                </td>
              </tr>
              <tr>
                <td>15 Days</td>
                <td>
                  {"$"}
                  {fifteenPlan.base_15price}
                </td>
                <td>
                  {"$"}
                  {fifteenPlan.customer_15price}
                </td>
              </tr>
              <tr>
                <td>30 Days</td>
                <td>
                  {"$"}
                  {thirtyPlan.base_30price}
                </td>
                <td>
                  {"$"}
                  {thirtyPlan.customer_30price}
                </td>
              </tr>
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}
